<template lang="html">
  <div class="convert-vip-page">
    <!-- 头部 -->
    <default-head header="兑换VIP会员"></default-head>
    <count-item class="borderB" textLeft="我的召币" textRight="兑换明细" :isShow="true">
      <span class="middle wrongColor">1148币</span>
    </count-item>
    <convert-list :isAd="true" :isReach="true" :icon="icons[0]"></convert-list>
    <convert-list text="VIP2需要消耗20000召币" warn="您的VIP1级别未满，暂不能兑换VIP2" :isAd="true" :isReach="true" :icon="icons[1]"></convert-list>
    <convert-list text="VIP3需要消耗30000召币" warn="您的VIP2级别未满，暂不能兑换VIP3" :isAd="true" :isReach="false" :icon="icons[2]"></convert-list>
    <convert-list text="VIP4需要消耗40000召币" warn="您的VIP3级别未满，暂不能兑换VIP4" :isAd="true" :isReach="false" :icon="icons[3]"></convert-list>
    <convert-list text="VIP5需要消耗50000召币" warn="您的VIP4级别未满，暂不能兑换VIP5" :isAd="true" :isReach="false" :icon="icons[4]"></convert-list>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import countItem from '@/components/personcenter/countItem'
import convertList from '@/components/convertVip/convertList'

import v01 from '@/assets/img/convertVip/v01@2x.png'
import v02 from '@/assets/img/convertVip/v02@2x.png'
import v03 from '@/assets/img/convertVip/v03@2x.png'
import v04 from '@/assets/img/convertVip/v04@2x.png'
import v05 from '@/assets/img/convertVip/v05@2x.png'

export default {
  data () {
    return {
      icons: [v01, v02, v03, v04, v05]
    }
  },
  components: { defaultHead, countItem, convertList }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.convert-vip-page {
  padding-top: .88rem;
  .middle {
    font-size: .36rem;
  }
}

</style>



